<template>
    <div>
        <div class="kd-page-title">
            输入业务编号
        </div>
        <div class="kd-page-content-wrapper wrap">
            <kd-full-input
                :ime-type="3"
                class="inputAccount"
                placeholder="请输入业务编号"
                :watch-data="getAccount"
            />
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="查询"
                :button-handle="nextStep"
                :but-disable="!number"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';

export default {
    name: 'PassportAccreditationInfo',
    computed: {
        ...mapState('common', {
            businessTypeCode: 'businessTypeCode'
        }),
        ...mapState('exitProcess', {
            number: 'number'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep']),
        ...mapMutations('exitProcess', ['setNumber']),
        getAccount (data) {
            this.setNumber(data);
        },
        nextStep () {
            this.changeStep('down');
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        .inputAccount {
            width: 50%;
            color: #000000;
        }
    }
</style>
