<template>
    <div>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <input-info v-if="currentStep === steps.findIndex(step=>step.key === 'inputInfo')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="getQueryInfo"
        />
        <person-info v-if="currentStep === steps.findIndex(step=>step.key === 'personInfo')"/>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import process from '@/components/process';
import personInfo from './components/person-info';
import inputInfo from './components/input-info';

import { exitEntryProgress } from '@/service/exitEntry/entryAndExit';
import { CRJ_PROCESS } from '@/constant/constant';
import { EXIT_ENTRY_PASSPORT } from '@/constant/stepsOption';

export default {
    name: 'PassportAccreditationIndex', // 护照办理进度查询
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        process,
        personInfo,
        inputInfo
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            identifyCard: 'identifyCard',
            binocularCamera: 'binocularCamera',
            businessTypeCode: 'businessTypeCode'
        }),
        ...mapState('exitProcess', {
            number: 'number'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage', 'setSteps']),
        ...mapMutations('exitProcess', ['setProcessInfo']),
        async getQueryInfo () {
            // 人脸识别认证 护照进度查询
            const params = {
                ywbh: this.number,
                idNum: this.identifyCard.certNumber,
                identifiedPhoto: this.binocularCamera,
                applyType: CRJ_PROCESS[this.businessTypeCode] // 1港澳台 2护照查询
            };
            const result = await exitEntryProgress(params);
            if (result.code === '0') {
                if (result.result) {
                    const data = [];
                    if (result.result.applyProgressHZDTO) {
                        data.push(result.result.applyProgressHZDTO);
                    }
                    if (result.result.applyProgressHKMACDTO) {
                        data.push(result.result.applyProgressHKMACDTO);
                    }
                    if (result.result.applyProgressTWDTO) {
                        data.push(result.result.applyProgressTWDTO);
                    }
                    this.setProcessInfo(data);
                    this.changeStep('down');
                } else {
                    this.changeTipPage({
                        message: '查询无结果',
                        imgName: 'imgBox'
                    });
                }
            } else {
                this.changeTipPage({
                    message: result.message,
                    imgName: 'imgBox'
                });
            }
        }
    },
    mounted () {
        this.setSteps(EXIT_ENTRY_PASSPORT);
    }
};

</script>
